*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: #F6F6F6;
    /* padding: 0 5rem 0 5rem; */
}

.navbar{
    background-image: url("C:\Users\ASUS\Desktop\Reactproj\FoodSite\foodsite\src\Assests\home-banner-background.png");
    background-position:right;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
   

}

nav{
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    padding: 10px 0;
    height: 10vh;
    padding: 0 5rem 0 5rem;
} 



nav ul{
    width: 100%;
    text-align: right;
    /* border: 5px solid rgb(0, 217, 255); */

}


.navbar ul li{
    display: inline-block;
    /* padding: 1rem; */
    padding-right: 60px;

}

.navbar ul a{
    list-style-type: none;
    text-decoration: none;
    color:black;
    
}

.btn{
    display: flex;
  align-items: center;
  padding: 10px 20px;
  border: 0;
  outline: 0;
  border-radius: 30px;
  background: #f7f3f5;
  color: #0f0e0e;
  font-weight: 500;
  cursor: pointer;
  margin-right: 20px;
}

