/* Work */

.main-work{
    min-height: 100vh;
    margin-top: 100px;
    margin: 100px 60px 0 60px;

}

.wth{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 50%;
    align-items: center;
    margin: auto;
    /* border: 5px solid black; */
