    padding-top: 1rem;
}

.Wk{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
}

.work{
    padding: 1rem;
    text-align: center;
    background-color: white;
}

.work h2{
    padding: 1rem 0 1rem 0;
}


